@import "../global/variables";

.register {
  border: 2px solid #ff9900;
  border-radius: $border-radius;

  i {
    opacity: 0.4 !important;
    filter: alpha(opacity=50) !important; /* For IE8 and earlier */
  }

  .title {
    font-family: 'Exo 2', 'Open Sans', 'Droid Sans', sans-serif;
    line-height: 40px;
    vertical-align: middle;
    font-size: 18px;
    height:44px;

    color: #FFF;
    background-color: #ff9900;
    padding: 0px 20px;

    i {
      float: right;
      line-height: 44px;
      margin-left: 15px;


      .fa-times {
        font-size: 18px;
      }
    }
    i:hover{
      cursor:pointer;
    }
  }

  >.content {
    padding: 8px;
    background-color: white;

    .header {
      font-family: 'Exo 2', 'Open Sans', 'Droid Sans', sans-serif;
      font-size: 14px;
      font-weight: bold;
      color: #ff9900;
      line-height: 40px;
      padding: 0 8px;
    }

    > .row {
      padding: 0 8px;
      overflow-y: auto;
      overflow-x: hidden;

      > .column {
        width: 50%;
        float: left;

        > .row {
          line-height: 30px;
          overflow-y: auto;
          overflow-x: hidden;
          position: relative;

          label {
            font-family: 'Open Sans', 'Droid Sans', Verdana, sans-serif;
            font-size: 11px;
            width: 40%;
            float: left;
            color: black;
            display: inline-block;
          }

          .field {
            font-family: 'Open Sans', 'Droid Sans', Verdana, sans-serif;
            font-size: 12px;
            width: 60%;
            float: left;
            padding-top: 5px;
            background-color: #FFF;

            &.top {
              padding-top: 0;
              i {
                position: absolute;
                right: 6px;
                top: 4px;
              }
            }

            &.birth-date {
              select {
                width: 33.3333333%;
              }
            }

            input[type="text"],
            input[type="password"],
            input[type="email"] {
              float: left;
              border: 1px solid black;
              border-radius: $border-radius;
              width: 100%;
            }
            input[type="checkbox"] {

            }

            select {
              float: left;
              border-radius: $border-radius;
              border: 1px solid lightgray !important;
              height: 20px;
              min-width: 50px;
              background-color: #FFF;
              width: 100%;
            }
          }
        }
      }
    }

    .bs-wp {
      padding: 0 16px;

      .col-xs-3,
      .col-xs-4,
      .col-xs-5,
      .col-xs-6 {
        padding: 0 5px;
      }
      label {
        font-family: 'Open Sans', 'Droid Sans', Verdana, sans-serif;
        font-size: 11px;
        color: black;
        font-weight: normal;
      }
      input[type="text"] {
        width: 100%;
        border: 1px solid black;
        border-radius: $border-radius;
        height: 21px;
        font-size: 12px;
      }

      .agreement {
        font-family: 'Open Sans', 'Droid Sans', Verdana, sans-serif;
        font-size: 12px;
        line-height: 30px;
        margin-left: -24px; /* change later*/
        .check {
          padding-right: 5px;
          padding-top: 5px;
          float: left;
        }
        .fa-exclamation-circle,
        .fa-check-circle {
          margin-left: 5px;
        }
      }
    }
  }

  .fa-exclamation-circle,
  .fa-check-circle {
    font-size: 1.4em;
    margin-left: -20px;
    margin-top: 2px;
  }

  .warning-color {
    color: red;
  }
  .success-color {
    color: green
  }

  >.footer {
    overflow: auto;
    padding: 12px;
    background-color:white;
    padding-bottom:15px;
    padding-left:16px;

    .captcha {
      * {
        box-sizing: border-box;
      }
      width: 60%;

      .img {
        float: left;
        width: 168px;
        border: 1px solid #ccc;
        border-radius: $border-radius 0 0 $border-radius;
        overflow: hidden;
      }
      .codigo {
        position: relative;
        float: left;
        width: 168px;
        border: 1px solid #ccc;
        border-left: none;
        border-radius: 0 $border-radius $border-radius 0;
        .refresh {
          float: left;
          width: 30px;
          line-height: 40px;
          button {
            height: 40px;
            border: 0;
            background: none;
          }
        }
        input {
          float: left;
          width: 130px;
          border: none !important;
          height: 40px;
          line-height: 40px;
        }
        span {
          position: absolute;
          bottom: 0;
          left: 0;
        }
      }
    }

    .actions {
      button {
        font-family: 'Exo 2', 'Open Sans', 'Droid Sans', sans-serif;
        font-weight: bold;
        font-size:15px;
        color: #FFF;
        float: right;
        margin: 0 5px;
        background: #ccc;
        height: 40px;
        border: none;
        border-radius: $border-radius;
        width: 110px;

        &.submit {
          background: $cor-verde-submit;
          &:hover {
            background: lighten($cor-verde-submit, 10%);
          }
        }
        &:hover {
          background-color: lighten(#ccc, 10%);
        }

      }
    }
  }

  .g-recaptcha {
    padding-top: 10px;
  }
}

