.flat a {
  background: lightgrey;
  font-family: 'Exo 2', 'Open Sans', 'Droid Sans', sans-serif !important;
  color: white;
  font-size: 14px;
  transition: all 0.5s;
  height: 45px;
  &:after {
    background: lightgrey;
    font-family: 'Exo 2', 'Open Sans', 'Droid Sans', sans-serif !important;
    color: white;
    font-size: 14px;
    transition: all 0.5s;
    height: 45px;
  }
  &:before {
    background: white;
    box-shadow: 0 0 0 1px #ccc;
    font-family: 'Exo 2', 'Open Sans', 'Droid Sans', sans-serif !important;
  }
  &:hover, &.active, &:hover:after, &.active:after {
    font-family: 'Exo 2', 'Open Sans', 'Droid Sans', sans-serif !important;
    background: #ff9900;
  }

  &:last-child.active {
    border-radius: 0 5px 5px 0;
    /*this was to prevent glitches on hover*/
    padding: 0px;
    color: #FFF;
    background: #FFF;
    height: 0px;
  }
}